                                    <div class='swf-img-i3'
                                         v-else-if="((item.url.length===3)||(item.url.length>3))&&(item.type===2)">
                                        <template v-for="(imgItem,index)  in item.url">
                                            <img :data-moments-img='imgItem.url' :data-pic-index='index'
