<script>
  // @ is an alias to /src
  import { ImagePreview } from 'vant'
  import defaultPage from '../../components/DafultPage'

  export default {
    name: 'detailTopic',
    data() {
      return {
        active: 0,
        topicInfo: {},
        user: {},
        tabs: {
          tabName: ['最新', '热门']
        },
        showAd: true, //是否显示广告
        picLargeArr: [], //大图的数组
        largeIndex: Number, //大图当前滑块的索引
        swfVideo: [],
        hots: [],// 最热
        latest: [] //最新
      }
    },
    components: {
      defaultPage
    },
    created() {
      document.title = '话题详情';
    },
    mounted: function () {
      this.getData();
      for (let i = 0; i < 2; i ++) {
        this.getTopicData(i);
      }
    },
    methods: {
      getData: function (type = 0) {
        let params = this.$queryParams(location.href);
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE3 + '/towntalk/towntalkDetail', //话题详情
          data: this.$qs.stringify({
            id: params.id,
            userId: params.userId
          })
        }).then((res) => {
          if (res !== null) {
            if (res.data.status === 1000) {
              this.topicInfo = res.data.data;
              this.user = res.data.data.user;
            } else {
              // this.$toast(res.data.msg);
            }
          }
        }).catch((err) => {
          console.warn(err);
        })
      },
      playVideo: function (index, tabIndex) {
        let parent = document.getElementsByClassName('item-sweetfans')[index];
        let videos = document.getElementsByTagName('video');
        for (let i = 0; i < videos.length; i ++) {
          videos[i].pause();
        }
        let v = parent.getElementsByTagName('video')[0];
        if (v.readyState >= 2) {
          v.play();
          parent.getElementsByClassName('load-layer')[0].classList.add('display');
        }
      },
      getTopicData: function (type = 0) {
        let params = this.$queryParams(location.href);
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE3 + '/dynamic/dynamicList',
          data: this.$qs.stringify({
            page: 0,
            id: params.id,
            userId: params.userId,
            sortType: type,
            type: 2
          })
        }).then((res) => {
          if (res !== null) {
            if (type === 0) {
              if (res.data.status === 1000) {
                let data = res.data.data;
                this.hots = data;
                for (let i = 0; i < data.length; i ++) {
                  if (data[i].notice) {
                    data[i].title = this.$atSomebody(data[i].notice, data[i].title);
                  }
                  let url = this.hots[i];
                  this.hots[i].poster = url[0] + '?vframe/jpg/offset/4/h/960';
                }
                for (let j = 0; j < this.hots.length; j ++) {
                  if (this.hots[j].url === null) {
                    this.hots[j].url = [];
                  }
                  if (this.hots[j].location) {
                    this.hots[j].location = this.hots[j].location.split(',');
                  }
                  if (this.hots[j].type === 3) {
                    this.hots[j].vPoster = this.hots[j].url[0] + '?vframe/jpg/offset/4/h/450'
                  }
                  if (this.hots[j].type === 4) {
                    this.hots[j].vPoster = this.hots[j].url[0] + '?vframe/jpg/offset/4/h/960'
                  }
                }
              } else {
                this.hots = [];
                // this.$toast(res.data.msg);
              }
            }
            if (type === 1) {
              if (res.data.status === 1000) {
                let data = res.data.data;
                for (let i = 0; i < data.length; i ++) {
                  if (data[i].notice) {
                    data[i].title = this.$atSomebody(data[i].notice, data[i].title);
                  }
                }
                this.latest = data;
                for (let j = 0; j < this.latest.length; j ++) {
                  if (this.latest[j].url === null) {
                    this.latest[j].url = [];
                  }
                  if (this.latest[j].location) {
                    this.latest[j].location = this.latest[j].location.split(',');
                  }
                  if (this.latest[j].type === 3) {
                    this.latest[j].vPoster = this.latest[j].url[0] + '?vframe/jpg/offset/4/h/450'
                  }
                  if (this.latest[j].type === 4) {
                    this.latest[j].vPoster = this.latest[j].url[0] + '?vframe/jpg/offset/4/h/960'
                  }
                }
              } else {
                this.latest = [];
                // this.$toast(res.data.msg);
              }
            }
          }
        }).catch((err) => {
          console.warn(err);
        })
      },
      //是否显示广告
      isShowAd: function () {
        this.$toast('关闭成功');
        this.showAd = false;
      },
      changeActive: function () {
        // alert(this.active);
      },
      //查看大图
      viewPicture: function (i, d) {
        this.picLargeArr = d;
        ImagePreview({
          images: d,
          startPosition: i
        })
      }
    }
  }
</script>
<style scoped lang="scss">
    a {
        color: #0A66FF;
    }

    .display {
        display: none;
    }

    @mixin radius50 {
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
