    }

    @mixin wh100 {
        width: 100%;
        height: 100%;
    }

    @mixin radius1 {
        -webkit-border-radius: .1rem;
        -moz-border-radius: .1rem;
        border-radius: .1rem;
    }

    @mixin radius-bom1 {
        -webkit-border-radius: 0 0 .1rem .1rem;
        -moz-border-radius: 0 0 .1rem .1rem;
        border-radius: 0 0 .1rem .1rem;
    }

    @mixin line2 {
        overflow: hidden;
        text-overflow: ellipsis;
