    }

    .van-tabs__wrap {
        width: 100%;
        border-bottom: 1px solid rgba(242, 242, 242, 1);
        margin: 0 auto;
    }

    .van-tab--active span.van-ellipsis {
        color: #FF2E4D !important;
    }

